%post-full-content{
    border-bottom: 3px solid $c5;
    color: $c4;

    .post-title{
        margin-bottom: 0;
        color: $c1;
        @include rem(font-size, 36px);
    }

    @for $index from 1 through 6 {
        h#{$index} {
            color: $c1;
        }
    }


    .metadata{
        margin-top: 0;
    }

    p{
        color: $c4;
        @include rem(font-size, 18px);
        line-height: 1.5em;
    }

    p:empty {
        display: none;
    }


}


/* == WordPress WYSIWYG Editor Styles == */

.entry-content img {
  margin: 0 0 1.5em 0;
}

.alignleft,
img.alignleft {
  margin-right: 1.5em;
  display: inline;
  float: left;
}

.alignright,
img.alignright {
  margin-left: 1.5em;
  display: inline;
  float: right;
}

.aligncenter,
img.aligncenter {
  margin-right: auto;
  margin-left: auto;
  display: block;
  clear: both;
}

.alignnone,
img.alignnone {
  /* not sure about this one */
}

.wp-caption {
  margin-bottom: 1.5em;
  text-align: center;
  padding-top: 5px;
}

.wp-caption img {
  border: 0 none;
  padding: 0;
  margin: 0;
}

.wp-caption p.wp-caption-text {
  line-height: 1.5;
  font-size: 10px;
  margin: 0;
}

.wp-smiley {
  margin: 0 !important;
  max-height: 1em;
}

blockquote.left {
  margin-right: 20px;
  text-align: right;
  margin-left: 0;
  width: 33%;
  float: left;
}

blockquote.right {
  margin-left: 20px;
  text-align: left;
  margin-right: 0;
  width: 33%;
  float: right;
}

.gallery dl {}

.gallery dt {}

.gallery dd {}

.gallery dl a {}

.gallery dl img {}

.gallery-caption {}

.size-full {
    width: 100%;
    height: auto;
}

.size-large {}

.size-medium {}

.size-thumbnail {}



