header.blog-home{
    position: relative;
    padding: 30px 0 20px;
    display: block;
    background-image: linear-gradient(-180deg, $c1 0%, $c2 100%);

    &:before{
        @extend %pseudo_icon;
        height: 100%;
        width: 100%;
        pointer-events: none;
    }

}



