%blured-hover{

  &:before{
    @extend %pseudo_icon;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba($c1, 0.4);
    opacity: 0;
    pointer-events: none;
    transition: .75s opacity $transition;
  }
  
  &:hover{

    img{
        filter:blur(3px);
    }

    &:before{
        opacity: 1;
    }

  }

}



